@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --color-bg-primary: #0e1118;
  --color-bg-secondary: #141722;
  --color-bg-tertiary: #1e2330;
  --color-bg-highlight: #252b3b;
  --color-text-primary: #fff;
  --color-text-secondary: #8a94a8;
  --color-text-tertiary: #636d83;
  --color-border: #2c3344;
  --color-green: #16c784;
  --color-red: #ea3943;
  --color-blue: #4878ff;
}

@keyframes slide-in {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.animate-slide-in {
  animation: slide-in 0.3s ease-out forwards;
}

@keyframes pulseGreen {
  0%,
  100% {
    border-color: rgba(34, 197, 94, 0.5);
    box-shadow: 0 0 0 0 rgba(34, 197, 94, 0.5);
  }
  50% {
    border-color: rgba(34, 197, 94, 1);
    box-shadow: 0 0 0 4px rgba(34, 197, 94, 0.3);
  }
}

.animate-pulse-green {
  animation: pulseGreen 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* Make sure sticky columns work and table scrolls horizontally */
table {
  table-layout: fixed;
}
th,
td {
  white-space: nowrap;
}
